<template>
<div class="user-circle" @click="menu = true">
  <div class="avatar" :style="objStyles">
    <input type="file" @change="onFileChange" accept="image/*" style="display: hidden" ref="input">
    <div v-if="user.birthday" class="zodiac-icon" :style="{borderColor: user.color}">
      <v-icon small>mdi-zodiac-{{ getZodiacName(user.birthday) }}</v-icon>
    </div>
  </div>

  <div>
    <span class="name">{{ user.name || 'Lover' }}</span>
  </div>

  <div class="gender">
    <v-icon small color="#00b4d2" v-if="user.gender == 1">mdi-gender-male</v-icon>
    <v-icon small color="#ff48ac" v-if="user.gender == 2">mdi-gender-female</v-icon>
    <span>{{ user.birthday }}</span>
  </div>

  <v-dialog v-model="dialogName" max-width="290">
    <v-card>
      <v-card-title>Edit name</v-card-title>
      <v-card-text>
        <v-text-field v-model="userName"></v-text-field>
      </v-card-text>
      <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialogName = false">Close</v-btn>
          <v-btn color="blue darken-1" text @click="saveName">Save</v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>

  <v-dialog v-model="dialogColor" max-width="290">
    <v-card>
      <v-card-title>Select Color</v-card-title>
      <v-card-text>
        <v-color-picker v-model="userColor" flat showSwatches hide-inputs hide-mode-switch hide-canvas mode="hex"></v-color-picker>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="dialogColor = false">Close</v-btn>
        <v-btn color="blue darken-1" text @click="saveColor">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <v-dialog v-model="dialogBirthday" max-width="290">
    <v-date-picker v-model="userBirthday">
      <v-spacer></v-spacer>
      <v-btn color="blue darken-1" text @click="dialogBirthday = false">Close</v-btn>
      <v-btn color="blue darken-1" text @click="saveBirthday">Save</v-btn>
    </v-date-picker>
  </v-dialog>

  <v-bottom-sheet v-model="menu">
      <v-list dense>
        <v-list-item link @click="selectImage">
          <v-list-item-icon><v-icon>mdi-account-circle-outline</v-icon></v-list-item-icon>
          <v-list-item-content>Select avatar</v-list-item-content>
        </v-list-item>
        <v-list-item link @click="showDialogName">
          <v-list-item-icon><v-icon>mdi-textbox</v-icon></v-list-item-icon>
          <v-list-item-content>Edit nick name</v-list-item-content>
        </v-list-item>
        <v-list-item link @click="showDialogColor">
          <v-list-item-icon><v-icon>mdi-eyedropper-variant</v-icon></v-list-item-icon>
          <v-list-item-content>Select border color</v-list-item-content>
        </v-list-item>
        <v-list-item link @click="showDialogBirthday">
          <v-list-item-icon><v-icon>mdi-cake</v-icon></v-list-item-icon>
          <v-list-item-content>Select birthday</v-list-item-content>
        </v-list-item>
        <v-list-item link @click="changeUserGender">
          <v-list-item-icon><v-icon>mdi-gender-male-female</v-icon></v-list-item-icon>
          <v-list-item-content>Change gender</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-bottom-sheet>
</div>
</template>

<script>
import { getZodiacName } from '@/utils/zodiac'

export default {
  props: {
    user: {
      type: Object,
      required: true,
    },
    name: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      menu: false,
      dialogName: false,
      dialogColor: false,
      dialogBirthday: false,
      avatar: 'https://c7.uihere.com/files/981/1001/235/q-version-cartoon-avatar-comics-significant-other-hand-painted-cartoon-cute-boy.jpg',
      userName: '',
      userColor: '',
      userBirthday: null,
    }
  },
  computed: {
    objStyles() {
      let styles = {}
      styles.backgroundImage = 'url(' + this.avatar + ')'
      styles.borderColor = this.user.color
      return styles
    }
  },
  mounted() {
    if(this.user.avatar.size) {
      this.avatar = URL.createObjectURL(this.user.avatar)
    }
  },
  methods: {
    getZodiacName,
    selectImage() {
      this.$refs.input.click()
    },
    onFileChange() {
      if (this.$refs.input.files.length) {
        this.$store.commit('SET_AVATAR', {
          name: this.name,
          file: this.$refs.input.files[0]
        })
        this.menu = false
      }
    },
    showDialogName() {
      this.userName = this.user.name
      this.dialogName = true
      this.menu = false
    },
    showDialogColor() {
      this.userColor = this.user.color
      this.dialogColor = true
      this.menu = false
    },
    showDialogBirthday() {
      this.dialogBirthday = true
      this.userBirthday = this.user.birthday
      this.menu = false
    },
    changeUserGender() {
      if (this.user.gender == 1) {
        this.$store.commit('SET_USER_GENDER', {
          name: this.name,
          value: 2
        })
      } else if (this.user.gender == 2) {
        this.$store.commit('SET_USER_GENDER', {
          name: this.name,
          value: 1
        })
      }
      this.menu = false
    },
    saveName() {
      this.$store.commit('SET_NAME', {
        value: this.userName,
        name: this.name
      })
      this.dialogName = false
    },
    saveColor() {
      this.$store.commit('SET_COLOR', {
        value: this.userColor,
        name: this.name
      })
      this.dialogColor = false
    },
    saveBirthday() {
      this.$store.commit('SET_BIRTHDAY', {
        name: this.name,
        value: this.userBirthday
      })
      this.dialogBirthday = false
    }
  },
  watch: {
    'user.avatar'(blob) {
      if(blob.size) {
        this.avatar = URL.createObjectURL(blob)
      }
    }
  }
}
</script>

<style lang="scss">
.user-circle {
  display: inline-block;
  margin: 10px;

  .avatar {
    width: 120px;
    height: 120px;
    border: 2px solid #ff30a2;
    border-radius: 50%;
    background-size: cover;
    position: relative;

    .zodiac-icon {
      position: absolute;
      background-color: white;
      border: 2px solid #0099ff;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      bottom: 0px;
      left: 50%;
      transform: translate(-50%, 50%);
    }
  }

  input[type="file"] {
    display: none;
  }

  .name {
    font-family: 'Pacifico', cursive;
    padding: 2px 12px;
    background-color: rgba(129, 129, 129, 0.4);
    border-radius: 10px;
    display: inline-block;
    margin-top: 18px;
    color: #ffffff;
  }
  .gender {
    color: #ffffff;
    background-color: rgba(129, 129, 129, 0.4);
    padding: 4px 10px;
    border-radius: 10px;
    display: inline-block;
    margin-top: 6px;
    font-size: 13px;
    .v-icon {
      margin-right: 4px;
    }
  }
}
</style>