<template>
<v-app>
  <main-screen></main-screen>
  <locked-screen @ok="locked = false" v-if="locked"></locked-screen>
  <splash-screen @ready="onReady" v-if="!ready"></splash-screen>
</v-app>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      ready: false,
      locked: false
    }
  },
  created() {
    window.addEventListener('blur', this.onBlur)
  },
  computed: {
    ...mapState({
      hasPassCode: state => Boolean(state.pass_code)
    }),
  },
  beforeDestroy() {
    window.removeEventListener('blur', this.onBlur)
  },
  methods: {
    onReady() {
      this.ready = true
      if (this.hasPassCode) {
        this.locked = true
      }
    },
    onBlur() {
      if (this.hasPassCode) {
        this.locked = true
      }
    }
  }
}
</script>