import { MutationTree } from "vuex";
import { RootState } from './types';

const mutations: MutationTree<RootState> = {
  SET_AVATAR(state, payload) {
    if (payload.name == 'user1') {
      state.user1.avatar = payload.file
    }
    if (payload.name == 'user2') {
      state.user2.avatar = payload.file
    }
  },
  SET_NAME(state, payload) {
    if (payload.name == 'user1') {
      state.user1.name = payload.value
    }
    if (payload.name == 'user2') {
      state.user2.name = payload.value
    }
  },
  SET_COLOR(state, payload) {
    if (payload.name == 'user1') {
      state.user1.color = payload.value
    }
    if (payload.name == 'user2') {
      state.user2.color = payload.value
    }
  },
  SET_DATE(state, date) {
    state.start_day = date
  },
  SET_PASS_CODE(state, code) {
    if (code.length === 4) {
      state.pass_code = code
    }
    if (code.length === 0) {
      state.pass_code = ''
    }
  },
  SET_USER_GENDER(state, payload) {
    if (payload.name == 'user1') {
      state.user1.gender = payload.value
    }
    if (payload.name == 'user2') {
      state.user2.gender = payload.value
    }
  },
  SET_BIRTHDAY(state, payload) {
    if (payload.name == 'user1') {
      state.user1.birthday = payload.value
    }
    if (payload.name == 'user2') {
      state.user2.birthday = payload.value
    }
  }
}

export default mutations